import React, { useState } from 'react';
import { Form, Input, Button, message, Tooltip, Icon } from 'antd';
import { Link } from 'react-router-dom';
import { injectIntl } from 'react-intl';
import { signup } from './../../services';
import styled from 'styled-components';
import './index.css';

const FormItem = Form.Item;

const Div = styled.div`
  display: flex;
  justify-content: center button {
    width: 50%;
  }
`;

const SignIn = props => {
  const {
    intl: { messages },
    form: { getFieldDecorator },
  } = props;

  const [loading, setLoading] = useState(false);
  const [confirmDirty] = useState(false);

  const handleReset = () => {
    props.form.resetFields();
  };

  const handleSubmit = e => {
    e.preventDefault();

    props.form.validateFields((err, values) => {
      if (!err) {
        const form = {
          fullName: values.fullName,
          email: values.email,
          password: values.password,
        };
        setLoading(true);
        signup(form)
          .then(() => {
            message.success(messages.signup.success);
            setLoading(false);
            handleReset();
          })
          .catch(error => {
            if (error.response.status === 400) {
              message.error(messages.signup.error.email);
            } else {
              message.error(messages.signup.error.all);
            }
            setLoading(false);
          });
      }
    });
  };

  const compareToFirstPassword = (rule, value, callback) => {
    const form = props.form;
    if (value && value !== form.getFieldValue('password')) {
      callback(messages.signup.rules.password.match);
    } else {
      callback();
    }
  };

  const validateToNextPassword = (rule, value, callback) => {
    const form = props.form;
    if (value && confirmDirty) {
      form.validateFields(['confirm'], { force: true });
    }
    callback();
  };

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 6 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };
  const tailFormItemLayout = {
    wrapperCol: {
      xs: {
        span: 24,
        offset: 0,
      },
      sm: {
        span: 14,
        offset: 6,
      },
    },
  };

  return (
    <section className="form-card">
      <div
        className="form-card__img"
        style={{
          backgroundImage: `url('${process.env.PUBLIC_URL}/images/dan-freeman-401296-unsplash.jpg')`,
          height: '200px',
        }}
      ></div>
      <div className="form-card__body p-lg-5 p-4">
        <section className="form-v1-container">
          <h2>Create an Account</h2>
          <Form onSubmit={handleSubmit} className="form-v1">
            <FormItem
              {...formItemLayout}
              label={
                <span>
                  {messages.signup.fullName} &nbsp;
                  <Tooltip title="What do you want other to call you?">
                    <Icon type="question-circle-o" />
                  </Tooltip>
                </span>
              }
              hasFeedback
            >
              {getFieldDecorator('fullName', {
                rules: [
                  {
                    required: true,
                    whitespace: true,
                    message: messages.signup.rules.fullName.required,
                  },
                ],
              })(<Input />)}
            </FormItem>
            <FormItem {...formItemLayout} label="E-mail" hasFeedback>
              {getFieldDecorator('email', {
                rules: [
                  {
                    type: 'email',
                    message: messages.signup.rules.email.type,
                  },
                  {
                    required: true,
                    message: messages.signup.rules.email.required,
                  },
                ],
              })(<Input />)}
            </FormItem>
            <FormItem {...formItemLayout} label={messages.signup.password} hasFeedback>
              {getFieldDecorator('password', {
                rules: [
                  {
                    required: true,
                    message: messages.signup.rules.password.required,
                  },
                  {
                    validator: validateToNextPassword,
                  },
                ],
              })(<Input type="password" />)}
            </FormItem>
            <FormItem {...formItemLayout} label={messages.signup.confirm} hasFeedback>
              {getFieldDecorator('confirm', {
                rules: [
                  {
                    required: true,
                    message: messages.signup.rules.password.confirm,
                  },
                  {
                    validator: compareToFirstPassword,
                  },
                ],
              })(<Input type="password" />)}
            </FormItem>

            <FormItem {...tailFormItemLayout}>
              <Button loading={loading} type="primary" htmlType="submit" className="btn-cta">
                {!loading ? messages.signup.btnSignUp : messages.signup.btnSignUpLoad}
              </Button>
            </FormItem>
            <FormItem {...tailFormItemLayout}>
              <Div style={{ marginTop: -10 }}>
                {messages.signup.or} &nbsp;&nbsp;
                <Link to="/main/signin" style={{ color: '#73d13d' }}>
                  {messages.signup.login}
                </Link>
              </Div>
            </FormItem>
          </Form>
        </section>
      </div>
    </section>
  );
};

const WrappedSignIn = Form.create()(injectIntl(SignIn));

export default WrappedSignIn;
